    width: 100px;
    height: 40px;
    position: relative;
    margin-top: 15px;
}
.add .button-48{
    border: none;
    border-radius: 0;
}
.delte button{
    width: 40%;
    height: 55%;
    border: none;
    cursor: pointer;
    background-color: unset;
    color: rgb(247, 8, 8);
    font-size: 20px;
    transition: 0.5s;
}
.delte button:hover{
    color: rgb(225, 54, 54);
    
}
.add button{
    width: 40%;
    height: 55%;
    border: none;
    cursor: pointer;
    background-color: unset;
    color: rgb(95, 93, 245);
